        "text": "<strong><span data-redactor-tag=\"span\" style=\"color: rgb(109, 110, 65);\">Описание:</span></strong><br /><br /> <span style=\"font-weight: 500;\">Очищающий гель для лица с AHA-кислотами c щадящей ph подходит даже для чувствительной или травмированной кожи, очищает от загрязнений и ороговевших клеток, матирует, возвращает коже здоровое сияние, мгновенно улучшает цвет лица, устраняет последствия таких негативных факторов, как солнце, стресс от девайсов, гормональные проблемы, прием лекарств и естественное старение. Кожа становится идеальной даже в том случае, если она подверглась негативному влиянию от курения. Восстанавливает энергетические ресурсы организма и пробуждает кожу, стимулируя процессы клеточного обмена и микроциркуляции крови и лимфы. Косметическая формула геля осветляет пигментные пятна и выравнивает тон и текстуру кожи. Средство рекомендовано к использованию в любом возрасте и в любое время года.</span><br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(111, 121, 64);\"><br />Основные активные компоненты: </span></strong><br /><br /><span style=\"font-weight: 500;\">Молочная, лимонная, гликолевая, винная кислоты, экстракты огурца, зеленого чая, аллантоин.<br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(111, 121, 64);\"><br />Способ применения:</span></strong><span style=\"\"><br /><br /></span></span><span style=\"font-weight: 500;\">Встряхнуть перед использованием. Нанести на влажное лицо, избегая области вокруг глаз. Вспенить и тщательно смыть водой. Использовать 2-3 раза в неделю.</span><br />",
