        "text": "<strong><span data-redactor-tag=\"span\" style=\"color: rgb(109, 110, 65);\">Описание:<br /><br /></span></strong><span style=\"font-weight: 500;\">Революционный состав сыворотки, воздействуя на архитектуру дермы, активируя выработку коллагена и эластина, эффективно улучшает тургор, увлажненность кожи, способствует коррекции овала лица. Позволяет проработать как поверхностные, так и более глубокие слои кожи без риска раздражений. Обладает эффектом soft-peeling, способностью питать и омолаживать дерму лица, зоны декольте, шеи, оказывает успокаивающее, регенерирующее и защитное действие (создается защитная пленка на поверхности дермы), компенсируя потерю влаги в результате поверхностной эксфолиации, ускоряет обновление клеток, стимулирует выработку коллагена, воздействует на механизмы естественной подтяжки кожи, выравнивает ее текстуру и тон.<br /></span><br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(112, 103, 55);\">Способ применения:</span></strong><br /><br /><span style=\"font-weight: 500;\">3 - 4 раза в неделю несколько капель сыворотки нанести на очищенную кожу лица и декольте. Эффект от ее применения заметен буквально сразу. Можно использовать в летний период в ночном уходе. При жирной коже будет достаточно одной сыворотки на ночь, при сухой через 15 минут нанести ночной уход в зависимости от потребностей кожи. В широтах с низкой солнечной активностью может использоваться и в дневном уходе – после очищения и перед защитным средством.</span><br /><br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(130, 108, 58);\">Основные активные компоненты:</span></strong><br /><br /><span style=\"font-weight: 500;\">Гликолевая кислота, Youper complex = экстракт почек букового дерева + трёхмолекулярный комплекс гиалуроновой кислоты.</span><br /><br />",
