        "text": "<strong><span data-redactor-tag=\"span\" style=\"color: rgb(109, 110, 65);\">Описание:<br /><br /></span></strong><span style=\"font-weight: 500;\">Высококонцентрированная сыворотка с мощным пролонгированным антиоксидантным эффектом, ускоряет обмен веществ, улучшает цвет кожи, стимулирует процессы регенерации клеток, оказывает увлажняющее и тонизирующее действие. Входящие в ее состав витамины находятся в жирорастворимой форме и адекватной концентрации, обеспечивают коже всесторонний оздоравливающий уход: <br /><br />А - повышает тонус, восстанавливает эпидермис, питает, смягчает, защищает, регенерирует и обновляет кожу. <br /><br />Е - антиоксидант, препятствует образованию свободных радикалов. Отличный помощник в борьбе с увяданием кожи, сохраняет естественную влагу, питает, защищает от уф-излучения. Предохраняет кожу и волосы от преждевременного старения. <br /><br /> F - совокупность незаменимых полиненасыщенных жирных кислот (линолевой, линоленовой, арахидоновой), важный компонент клеточных мембран. Участвует в кислородном и жировом обменах, устраняет воспаления, восстанавливает клетки, предотвращает шероховатость, сухость, шелушение, сыпь, образование перхоти, ломкость и выпадение волос. Является мощным антиоксидантом. <br /><br /> C - антиоксидант, повышает эластичность сосудов, стимулирует синтез коллагена, клеточное дыхание, регенерирует кожу. Активизирует микроциркуляцию, отбеливает кожу, уменьшает пигментацию. <br /><br /> В5 - увлажняет, успокаивает, снимает раздражение и зуд, обладает сильным омолаживающим и разглаживающим эффектом, восстанавливает и защищает кожу, снимает воспаление. <br /></span><br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(112, 103, 55);\">Показания к применению:<br /></span></strong><br /><span style=\"font-weight: 500;\"><strong></strong>Для любого типа кожи в том числе сухой, обезвоженной, фотоповрежденной, жирной кожи, а также с акне легкой степени.</span><span style=\"font-weight: 500;\"><strong></strong></span><br /><em></em><strong></strong><strong></strong><br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(112, 103, 55);\">Способ применения:</span></strong><br /><br /><span style=\"font-weight: 500;\">В качестве активного концентрата 2-3 капли сыворотки равномерно распределить на коже лица и шеи до полного впитывания. Сверху нанести крем или гель согласно типу кожи. Идеально совместим со всеми средствами \"Kosmoteros professionnel\" (Paris). Всего несколько капель комплекса обогатят любой шампунь, бальзам для волос, крем для лица или тела - 2 капли на 5 г основы. Готовое средство хранить в прохладном темном месте. Поскольку комплекс содержит жирорастворимые витамины в высокой концентрации, во избежание передозировки, его необходимо наносить на ограниченные участки кожи, соблюдая рекомендованные дозы.<br /></span><br /><strong><span data-redactor-tag=\"span\" style=\"color: rgb(130, 108, 58);\">Основные активные компоненты:<br /></span></strong><br /><span style=\"font-weight: 500;\">Комплекс EPI-factors, витамины А,Е,F.C.B5.эфирное масло лимона, растительные масла облепихи, виноградной косточки, жожоба, зародышей пшеницы, сладкого миндаля.</span><strong></strong><br />",
